import { Component, Input, Output, EventEmitter } from '@angular/core';
@Component({
    selector: 'option-text',
    template: require('./worksheet.option.text.component.html'),
    styles: []
})
export class OptionText {

    @Input() id: String;
    @Input() data: Object;
    @Input() label: String;
    @Output() onUpdate: EventEmitter = new EventEmitter();
    @Input() qasign:boolean;

    public onChange(value){
        this.data = value;
        this.publishChange();
    }

    publishChange(){
        this.onUpdate.emit({
            id: 'value',
            data: this.data
        });
    }

}



