import { Component, Input, Output, EventEmitter, SimpleChanges} from '@angular/core';
import {AppState} from '../../../../app.service';
import { Observations } from '../../observations.component';

@Component({
  selector: 'save-reset',
  template: require('./worksheet.section.savereset.component.html'),
  styles: []
})

export class SaveResetSection {

  @Input() id: String;
  @Input() disableWorksheet: boolean;
  @Input() studyId : String;
  @Output() onUpdate: EventEmitter = new EventEmitter();

  constructor(
    private appState:AppState
  ) {}

  onSave(){
    this.onUpdate.emit({
      instruction: "SAVE"
    });
  }

  onReset(){
    this.onUpdate.emit({
      instruction: "RESET"
    });
  }
}



